<template>
  <div class="top">
      <router-link class="top__back" :to="{name:'index'}"></router-link>
      <span class="top__title">{{title}}</span>
      <router-link class="header-home" :to="{name:'index'}"></router-link>
  </div>
</template>

<script>
export default {
    props:['title']
}
</script>

<style>
.top{
    position:relative;
    height:44px;
    border-bottom:1px solid #ddd;
    font:15px/45px a;
    color:rgba(0,0,0,.7);
    background:#efeff0;
}
.top__back{
    float:left;
    width:42px;
    height:44px;
}
.top__back::before{
    content:'';
    display:block;
    margin:15px 0 0 16px;
    width:10px;
    height:16px;
    background:url(../assets/img/back.png) no-repeat;
    background-size:10px;
}
.header-home{
    float:right;
    width:44px;
    height:44px;
    background:url(http://image.read.duokan.com/mfsv2/download/fdsc3/p01eZH42sBV7/QTS90E2ZVritaa.png) no-repeat center;
    background-size:16px;
}
</style>



