import Vue from 'vue'
import Router from 'vue-router'
import indexPage from "../pages/index.vue"
import bookPage from "../pages/book.vue"
import readerPage from "../pages/reader.vue"
import searchPage from "../pages/search.vue"

Vue.use(Router)

export default new Router({
  mode:"history",
  base:"/bookShow/",
  routes: [
    {
      name:'index',
      path:'/',
      component:indexPage,
      meta:{
        title:'书城首页',
        width:null,
        keepAlive: true
      }
    },
    {
      name:'book',
      path:'/book',
      component:bookPage,
      meta:{
        title:'书籍详情',
        width:null,
      }
    },
    {
      name:'reader',
      path:'/reader',
      component:readerPage,
      meta:{
        width:null,
      }
    },
    {
      name:'search',
      path:'/search',
      component:searchPage,
      meta:{
        title:'搜索',
        width:null,
        keepAlive: false
      }
    }
  ],
})


