<template>
  <div class="container" :style="{background:initBackground}" ref="container">
      <div class="m-article-action">
          <div class="article-action-mid" @click="hiddenSwitch"></div>
      </div>
      <div class="top-nav" v-if="sidebarHidden">
            <div class="icon-back" @click="$router.push({name:'index'})"></div>
            <div class="nav-title" @click="$router.push({name:'index'})">{{chapter_title}}</div>
      </div>
      <div class="fiction_chapter_title">
      </div>
      <div class="m-read-content" :style="{fontSize:initFontsize+'px'}" v-if="chapter!==''">
          <h4>{{chapter.t}}</h4>
          <p v-for="(item,index) in chapter.p" :key="index">
              {{item}}
          </p>
      </div>
      <div class="m-botton-bar" v-if="chapter!==''">
          <ul class="u-tab">
              <li class="prev_button" @click="next_chapter">上一章</li>
              <li class="next_buttom" @click="prev_chapter">下一章</li>
          </ul>
      </div>
      <div class="nav-pannel-bk" v-if="fontHidden"></div>
      <div class="nav-pannel" v-if="fontHidden">
          <div class="child-mod">
              <span>字号</span>
              <button class="font-size-button" @click="largeFont">大</button>
              <button class="font-size-button" @click="smallFont">小</button>
          </div>
          <div class="child-mod">
              <span>背景</span>
              <div class="bk-container bk-container-white" @click="backgroundSwitch('#f7eee5')">
                  <div class="bk-container-current" v-show="initBackground=='#f7eee5'"></div>
              </div>
              <div class="bk-container bk-container-paple" @click="backgroundSwitch('#e9dfc7')">
                  <div class="bk-container-current" v-show="initBackground=='#e9dfc7'"></div>
              </div>
              <div class="bk-container bk-container-grey" @click="backgroundSwitch('#a4a4a4')">
                  <div class="bk-container-current" v-show="initBackground=='#a4a4a4'"></div>
              </div>
              <div class="bk-container bk-container-green" @click="backgroundSwitch('#cdefce')">
                  <div class="bk-container-current" v-show="initBackground=='#cdefce'"></div>
              </div>
              <div class="bk-container bk-container-blue" @click="backgroundSwitch('#283548')">
                  <div class="bk-container-current" v-show="initBackground=='#283548'"></div>
              </div>
          </div>
      </div>
       <div class="bottom-nav-bk" v-if="sidebarHidden"></div>
       <div class="bottom-nav" v-if="sidebarHidden">
        <div class="item">
            <div class="item-warp">
                <div class="icon icon-menu"></div>
                <div class="icon-text">
                    目录
                </div>
            </div>
        </div>
        <div class="item">
            <div class="item-warp" :class="{current:fontHidden}" @click="fontHiddenSwitch">
                <div class="icon icon-ft"></div>
                <div class="icon-text">
                    字体
                </div>
            </div>
        </div>
        <div class="item">
            <div class="item-warp" v-if="nightMode" @click="nightModeSwitch">
                <div class="icon icon-day"></div>
                <div class="icon-text">
                    白天
                </div>
            </div>
            <div class="item-warp" v-if="!nightMode" @click="nightModeSwitch">
                <div class="icon icon-night"></div>
                <div class="icon-text">
                    夜间
                </div>
            </div>
        </div>
    </div>

  </div>
</template>

<script>
import {readerStorage} from "../util/readerLocalstoarge"
import base64 from "js-base64"
import {jsonp,jsonpPromise} from "../util/jsonp"
export default {
    data(){
        return {
            initFontsize:16,
            initBackground:'#e9dfc7',
            sidebarHidden:false,
            fontHidden:false,
            nightMode:false,
            chapter_id:'',
            chapter:'',
            chapter_title:'',
            callback:'duokan_fiction_chapter'
        }
    },
    methods:{
        init(){
            //todo 初始化内容放置
            this.getLastChapterid();
            this.ReaderModel().getFictionInfo(this.ReaderModel().getChapterContent);
            this.getOldFontsize();
            this.getOldBackground();
            this.getnightMode();
        },
        prev_chapter(){
            if(this.chapter_id=='' || this.chapter_id ==4){return;}
            this.chapter_id+=1
            this.ReaderModel().getChapterContent()
            readerStorage.StorageSetter("last_chapter_id",this.chapter_id)
        },
        next_chapter(){
            if(this.chapter_id=='' || this.chapter_id==1){return;}
            this.chapter_id-=1
            this.ReaderModel().getChapterContent()
            readerStorage.StorageSetter("last_chapter_id",this.chapter_id)
            
        },
        ReaderModel(){
            //todo 实现和阅读器相关的数据交互方法
            let getFictionInfo=(callback)=>{
                    this.$axios.get('/ajax/chapter').then((res)=>{
                        if(res.data.result!==0){
                            return;
                        }
                        this.chapter_title=res.data.title
                        if(this.chapter_id==''){
                            this.chapter_id=res.data.chapters[1].chapter_id
                            this.chapter_id=parseInt(this.chapter_id,10)
                        }
                        callback && callback()
                })
            }
            let getChapterContent=()=>{
                    this.$axios.get(`ajax/chapter/data`,{params:{chapter_id:this.chapter_id}}).then((res)=>{
                        if(res.data.result!==0){
                            return;
                        }
                        let data =res.data;
                        let url =data.jsonp
                        jsonpPromise(url,this.callback).then((res)=>{
                            res = base64.Base64.decode(res)
                            this.chapter= JSON.parse(res)
                            window.scrollTo(0,-200)
                        })
                })
                    // this.$axios.get('ajax/chapter/newdata',{params:{chapter_id:this.chapter_id}})
                    // .then((res)=>{
                    //     this.chapter=res.data
                    //     window.scrollTo(0,-200)
                    // })
                    
            }
            return {
                getFictionInfo:getFictionInfo,
                getChapterContent:getChapterContent
            }
        },
        getLastChapterid(){
            let oldChapterid=readerStorage.StorageGetter('last_chapter_id')
            oldChapterid=parseInt(oldChapterid)
            if(!oldChapterid || oldChapterid===null){return;}
            this.chapter_id=oldChapterid
        },
        hiddenSwitch(){
            //todo 边栏显隐
            this.sidebarHidden=!this.sidebarHidden
            this.fontHidden=false;
        },
        fontHiddenSwitch(){
            this.fontHidden=!this.fontHidden
        },
        preventScroll(){
            //todo 防止边栏出现,不能滚动的需求
            if(this.sidebarHidden){
                event.preventDefault()
            }
        },
        largeFont(){
             if(this.initFontsize>19){
                return;
            }
            this.initFontsize+=1
            readerStorage.StorageSetter('font_size',this.initFontsize)
        },
        smallFont(){
            if(this.initFontsize<15){
                return;
            }
            this.initFontsize-=1
            readerStorage.StorageSetter('font_size',this.initFontsize)
        },
        backgroundSwitch(color){
            //todo 背景切换
            if(this.nightMode){
                return;
            }
            this.initBackground=color
            readerStorage.StorageSetter('background',this.initBackground)
        },
        scrollHidden(){
            //todo 滚动隐藏边栏
            document.addEventListener('scroll',()=>{
                this.sidebarHidden=false;
                this.fontHidden=false
            })
        },
        getOldFontsize(){
            //todo 获取用户设定的字号
            let oldFontsize=readerStorage.StorageGetter('font_size')
            oldFontsize=parseInt(oldFontsize)
            if(!oldFontsize || oldFontsize===null || oldFontsize>20 || oldFontsize<14){
                return;
            }
            this.initFontsize=oldFontsize
        },
        getOldBackground(){
            //todo 获取用户设定的背景
            let oldBackground=readerStorage.StorageGetter('background')
            if(!oldBackground || oldBackground===null){
                return;
            }else if(oldBackground=='#283548'){
                this.nightMode=true;
            }
            this.initBackground=oldBackground
        },
        nightModeSwitch(){
            //todo 黑夜模式切换
            this.nightMode=!this.nightMode
            let oldBackground =readerStorage.StorageGetter('background')
            if(this.nightMode){
                this.initBackground='#283548'
                readerStorage.StorageSetter('night_mode','night')
            }else{
                this.initBackground=oldBackground
                readerStorage.StorageSetter('night_mode','day')
            }
        },
        getnightMode(){
            //todo 获取用户是否设置了黑夜模式
            let oldnightMode=readerStorage.StorageGetter('night_mode');
            if(!oldnightMode || oldnightMode===null){
                return
            }
            if(oldnightMode==='night'){
                this.initBackground='#283548'
                this.nightMode=true
            }        
        }
    }, 
    mounted(){
        this.init()
        this.scrollHidden()
    }
}
</script>

<style>
/* @media screen and (max-width:736px){ .container{width: 100%;} }

@media screen and (min-width:736px){ .container{ width:60%; margin: 0 auto;} } */
.container{
    width:100%;
    text-align: left;
}
.m-read-content{
    color:#555;
    line-height:31px;
    padding:15px;
    max-width:800px;
    margin: 0 auto;
 }
.m-read-content h4{
    font-size:20px;
    color:#736357;
    border-bottom:1px solid #736357;
    letter-spacing:2px;
    margin:0 0 1em 0; 
}
.m-read-content p{
    text-indent: 2em;
    margin: 0.5em 0;
    letter-spacing: 0;
    line-height:24px;
}
.u-tab{
    height:34px;
    margin:10px auto;
    line-height:34px;
    border-radius:8px;
    border:1px solid #858382;
    font-size:12px;
    background: #000;
    opacity:0.9;
}
.u-tab li {
    display:inline-block;
    width:49%;
    border-right:1px solid #858382;
    text-align:center;
    color:#fff;
}
.u-tab li:nth-child(2){
    border-right:none
}
.m-botton-bar{
    width:70%;
    max-width:800px;
    padding: 5px;
    margin:0 auto;
}
.top-nav{
    position:fixed;
    top:0px;
    height:50px;
    width:100%;
    z-index:9999;
    background:#000;
}
.icon-back{
    position:absolute;
    top:14px;
    left:10px;
    width:23px;
    height:23px;
    background:url(data:image/png;base64,);
    background-size:contain;
}
.nav-title{
    position:absolute;
    top:16px;
    left:42px;
    color:#d5d5d6
}
.bottom-nav-bk {
    position: fixed;
    bottom: 0px;
    height: 70px;
    background: #000;
    width: 100%;
    opacity: 0.9;
    z-index: 998;
}
.nav-pannel-bk{
    position:fixed;
    bottom:70px;
    height:135px;
    width:100%;
    border-bottom:1px solid #333;
    background:#000;
    opacity:.9;
    z-index:10000;
}
.nav-pannel{
    position:fixed;
    bottom:70px;
    height:135px;
    width:100%;
    background:none;
    color:#fff;
    z-index:10001;
}
.child-mod{
    padding:5px 10px;
    margin:15px;
}
.child-mod span{
    display:inline-block;
    padding-right:20px;
    padding-left:10px;
}
.font-size-button{
    background:none;
    border:1px #8c8c8c solid;
    color:#fff;

    border-radius:16px;
    padding:5px 40px;
}
.child-mod button:nth-child(2){
    margin-right:10px;
}
.bk-container{
    position:relative;
    width:30px;
    height:30px;
    border-radius:15px;
    background:#fff;
    display:inline-block;
    vertical-align: -14px;
    margin-left:7px;
}
.bk-container-current{
    position:absolute;
    width:32px;
    height:32px;
    border-radius:16px;
    border:1px #ff7800 solid;
    display: inline-block;
    top:-2px;
    left:-2px;
}
.bk-container-white { background: #f7eee5; }
.bk-container-paple { background: #e9dfc7; }
.bk-container-grey { background: #a4a4a4; }
.bk-container-green { background: #cdefce; }
.bk-container-blue { background: #283548; }
.bottom-nav {
    position: fixed;
    bottom: 0px;
    height: 70px;
    width: 100%;
    background: none;
    z-index: 999;
    margin: 0 auto;
    text-align: center;
}
.item {
    display: inline-block;
    width: 32%;
    color: #fff;
    text-align: center;
    margin: 0 auto;
}
.item-warp {
    width: 26px;
    margin: 0 auto;
    position: relative;
}
.icon {
    position: absolute;
    top: 3px;
    left: 2px;
    width: 18px;
    height: 13px;
    background-size: contain;
}

.icon-menu {
    background: url(data:image/png;base64,);
    background-size: contain;
        }
.icon-ft {
    left:0;
    width: 20px;
    height: 13px;
    background: url(data:image/png;base64,);
    background-size: contain;
}

.current .icon-ft {
    top: 2px;
    left: -1px;
    width: 22px;
    height: 15px;
    background: url(data:image/png;base64,);
    background-size: contain;
        }

.icon-night {
    left: 4px;
    width: 16px;
    height: 16px;
    background: url(data:image/png;base64,);
    background-size: contain;
}

.icon-day {
    width: 19px;
    height: 18px;
    background: url(data:image/png;base64,);
    background-size: contain;
        }

.icon-text {
    position: absolute;
    top: 25px;
    font-size: 10px;
}
.article-action-mid{
    position:fixed;
    z-index:9999;
    width:100%;
    height:40%;
    top:30%;
}
</style>



